@import '_reset.scss';

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
   display: block;
}

audio,
canvas,
video {
   display: inline-block;
   *display: inline;
   *zoom: 1;
}

audio:not([controls]) {
   display: none;
   height: 0;
}

[hidden] {
   display: none;
}

html {
   font-size: 100%;
   -webkit-text-size-adjust: 100%;
       -ms-text-size-adjust: 100%;
   font-family: sans-serif;
}

button,
input,
select,
textarea {
   font-family: sans-serif;
}

body {
   margin: 0;
}

a {
   &:focus {
      outline: thin dotted;
   }
   &:active,
   &:hover {
      outline: 0;
   }
}

h1 {
   font-size: 2em;
   margin: .67em 0;
}

h2 {
   font-size: 1.5em;
   margin: .83em 0;
}

h3 {
   font-size: 1.17em;
   margin: 1em 0;
}

h4 {
   font-size: 1em;
   margin: 1.33em 0;
}

h5 {
   font-size: .83em;
   margin: 1.67em 0;
}

h6 {
   font-size: .75em;
   margin: 2.33em 0;
}

abbr[title] {
   border-bottom: 1px dotted;
}

b,
strong {
   font-weight: bold;
}

blockquote {
   margin: 1em 40px;
}

dfn {
   font-style: italic;
}

mark {
   background: #ff0;
   color: #000;
}

p,
pre {
   margin: 1em 0;
}

code,
kbd,
pre,
samp {
   font-family: monospace,serif;
   _font-family: 'courier new',monospace;
   font-size: 1em;
}

pre {
   white-space: pre;
   white-space: pre-wrap;
   word-wrap: break-word;
}

q {
   quotes: none;
   &:before,
   &:after {
      content: '';
      content: none;
   }
}

small {
   font-size: 80%;
}

sub {
   font-size: 75%;
   line-height: 0;
   position: relative;
   vertical-align: baseline;
}

sup {
   font-size: 75%;
   line-height: 0;
   position: relative;
   vertical-align: baseline;
   top: -.5em;
}

sub {
   bottom: -.25em;
}

dl,
menu,
ol,
ul {
   margin: 1em 0;
}

dd {
   margin: 0 0 0 40px;
}

menu,
ol,
ul {
   padding: 0 0 0 40px;
}

nav {
   ul,
   ol {
      list-style: none;
      list-style-image: none;
   }
}

img {
   border: 0;
   -ms-interpolation-mode: bicubic;
}

svg:not(:root) {
   overflow: hidden;
}

figure,
form {
   margin: 0;
}

fieldset {
   border: 1px solid #c0c0c0;
   margin: 0 2px;
   padding: .35em .625em .75em;
}

legend {
   border: 0;
   padding: 0;
   white-space: normal;
   *margin-left: -7px;
}

button,
input,
select,
textarea {
   font-size: 100%;
   margin: 0;
   vertical-align: baseline;
   *vertical-align: middle;
}

button,
input {
   line-height: normal;
}

button,
html input[type='button'] {
   -webkit-appearance: button;
   cursor: pointer;
   *overflow: visible;
}

input {
   &[type='reset'],
   &[type='submit'] {
      -webkit-appearance: button;
      cursor: pointer;
      *overflow: visible;
   }
}

button[disabled] {
   cursor: default;
}

input {
   &[disabled] {
      cursor: default;
   }
   &[type='checkbox'],
   &[type='radio'] {
      -webkit-box-sizing: border-box;
         -moz-box-sizing: border-box;
              box-sizing: border-box;
      padding: 0;
      *height: 13px;
      *width: 13px;
   }
   &[type='search'] {
      -webkit-appearance: textfield;
      -webkit-box-sizing: content-box;
         -moz-box-sizing: content-box;
              box-sizing: content-box;
      &::-webkit-search-cancel-button,
      &::-webkit-search-decoration {
         -webkit-appearance: none;
      }
   }
}

button::-moz-focus-inner,
input::-moz-focus-inner {
   border: 0;
   padding: 0;
}

textarea {
   overflow: auto;
   vertical-align: top;
}

table {
   border-collapse: collapse;
   border-spacing: 0;
}

* {
           box-sizing: border-box;
   -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
   -webkit-font-smoothing: antialiased;
           font-smoothing: antialiased;
   text-rendering: optimizeLegibility;
   &:after,
   &:before {
              box-sizing: border-box;
      -webkit-box-sizing: border-box;
         -moz-box-sizing: border-box;
      -webkit-font-smoothing: antialiased;
              font-smoothing: antialiased;
      text-rendering: optimizeLegibility;
   }
}

html {
   font-size: 100%;
}

body {
   font-family: Arial,'Helvetica Neue',Helvetica,sans-serif;
   font-size: 14px;
   color: #444;
   font-weight: 300;
}

.clearfix {
   &:before {
      display: table;
      content: '';
   }
   &:after {
      display: table;
      content: '';
      clear: both;
   }
}

.clear {
   clear: both;
}

.clearfix {
   *zoom: 1;
}

img {
   max-width: 100%;
   vertical-align: bottom;
}

a {
   &:focus,
   &:hover,
   &:active {
      outline: 0;
   }
}

.wrapper {
   max-width: 1280px;
   width: 95%;
   margin: 0 auto;
   position: relative;
}

::selection,
::-webkit-selection,
::-moz-selection {
   background: #04a4cc;
   color: #fff;
   text-shadow: none;
}

.alignnone {
   margin: 5px;
}

.aligncenter,
div.aligncenter {
   display: block;
   margin: 5px auto 5px auto;
}

.alignright {
   float: right;
   margin: 5px 0 20px 20px;
}

.alignleft {
   float: left;
   margin: 5px 20px 20px 0;
}

a img {
   &.alignright {
      float: right;
      margin: 5px 0 20px 20px;
   }
   &.alignnone {
      margin: 5px 20px 20px 0;
   }
   &.alignleft {
      float: left;
      margin: 5px 20px 20px 0;
   }
   &.aligncenter {
      display: block;
      margin-left: auto;
      margin-right: auto;
   }
}

.wp-caption {
   background: #fff;
   border: 1px solid #f0f0f0;
   max-width: 96%;
   padding: 5px 3px 10px;
   text-align: center;
   img {
      border: 0 none;
      height: auto;
      margin: 0;
      max-width: 98.5%;
      padding: 0;
      width: auto;
   }
   .wp-caption-text {
      font-size: 11px;
      line-height: 17px;
      margin: 0;
      padding: 0 4px 5px;
   }
}

.gallery-caption {
   font-size: 11px;
   line-height: 17px;
   margin: 0;
   padding: 0 4px 5px;
}

@media print {
   * {
      background: transparent !important;
      color: #000 !important;
      -webkit-box-shadow: none !important;
              box-shadow: none !important;
      text-shadow: none !important;
   }
   a {
      text-decoration: underline;
      &:visited {
         text-decoration: underline;
      }
      &[href]:after {
         content: ' (' attr(href) ')';
      }
   }
   abbr[title]:after {
      content: ' (' attr(title) ')';
   }
   .ir a:after {
      content: '';
   }
   a {
      &[href^='javascript:']:after,
      &[href^='#']:after {
         content: '';
      }
   }
   pre,
   blockquote {
      border: 1px solid #999;
      page-break-inside: avoid;
   }
   thead {
      display: table-header-group;
   }
   tr {
      page-break-inside: avoid;
   }
   img {
      page-break-inside: avoid;
      max-width: 100% !important;
   }
   p,
   h2,
   h3 {
      orphans: 3;
      widows: 3;
   }
   h2,
   h3 {
      page-break-after: avoid;
   }
}
